.Search {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-image: url('../../static/images/bg/main.png');
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
  background-color: #0xB9B4B4;
  position: relative;
  padding-top: 40px;
  overflow: hidden;
  overflow-y: auto;
}
.Search:before {
  content: '';
  z-index: 0;
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.4);
}
.Search_input {
  position: relative;
  padding-right: 25px;
}
.Search_input input[type=text] {
  font-size: 5em;
}
.Search_input label {
  font-size: 4em!important;
}
.Search_input label.active {
  font-size: 2em!important;
}
.Search_input_close {
  position: absolute;
  right: 0;
  top: 50%;
  font-size: 2.5em;
  color: #212121 !important;
}
.Search .card {
  margin-bottom: 10px;
}



