.Menu {
  width: 150px;
  overflow: hidden;
  z-index: 2;
  transition: 0.2s width ease-in;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
@media screen and (max-width: 768px) {
  .Menu {
    width: 0;
  }
}
.Menu_mobileMenu {
  padding: 5px 15px!important;
  z-index: 1;
  display: none!important;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 30px!important;
}
@media screen and (max-width: 768px) {
  .Menu_mobileMenu {
    display: block!important;
  }
}
.Menu_mobileMenu-active {
  width: 100px;
}
.Menu_nav {
  height: 100vh;
  flex-direction: column;
}
.Menu_nav_logo {
  color: #0099CC;
  width: 100%;
  border-bottom: 1px solid #33b5e5;
  display: flex!important;
  justify-content: center;
  align-items: center;
}
.Menu_nav_logo i {
  font-size: 5em;
}
.Menu_nav_bar {
  flex-direction: column;
}
.Menu_nav_bar_link {
  font-size: 4em;
  font-weight: bold;
  color: #00C851;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media screen and (max-height: 396px) {
  .Menu_nav_bar_link {
    font-size: 2em;
  }
}
.Menu_nav_bar_link_text {
  font-size: 14px;
  text-align: center;
  margin: 0;
  padding-top: 10px;
  opacity: 0;
  transform: translateY(-100%);
  transition: 250ms opacity ease-in, 250ms transform ease-in;
}
.Menu_nav_bar_link:hover .Menu_nav_bar_link_text {
  opacity: 1;
  transform: translateY(0);
}
.Menu_nav_bar_link-active {
  color: #007bff;
}
.Menu_nav_bar_link-active .Menu_nav_bar_link_text {
  opacity: 1;
  transform: translateY(0);
}



