.Sitemap {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  font-size: 3em;
  color: #00C851;
  transition: 250ms background-color ease-in-out;
  padding: 0 20px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.Sitemap:hover {
  background-color: rgba(32, 197, 91, 0.2);
  cursor: pointer;
}
.Sitemap-hide {
  display: none;
}



