.App {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: flex-start;
}
.animatedWrapper {
  width: 100%;
  position: relative;
}
.anim {
  width: 100%;
}
.anim-appear {
  position: absolute;
  top: 0;
  left: -100%;
  height: 100%;
  width: calc(100% - 150px);
  opacity: 0.1;
}
.anim-appear.anim-appear-active {
  left: 0;
  opacity: 1;
  transition: left 0.7s ease-in-out, opacity 1.5s ease;
}



