.SitemapWrap {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 300px;
  background-color: #fff;
  overflow: hidden;
  overflow-y: auto;
  transition: 250ms transform ease-in-out;
  transform: translateX(100%);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.SitemapWrap .expPanel {
  box-shadow: none;
  border-bottom: none;
  padding-left: 10px;
}
.SitemapWrap .expPanel .panelTitle {
  border-bottom: 1px solid rgba(0, 200, 81, 0.1);
  font-weight: 500;
}
.SitemapWrap .expPanel .panelTitle .title {
  padding: 0;
  color: #0099CC;
}
.SitemapWrap .expPanel .panelTitle .actions .mdl-button {
  background-color: transparent;
  border: none;
  outline: none;
}
.SitemapWrap .expPanel .panelTitle .text {
  margin: 0;
}
.SitemapWrap .expPanel .panelContent {
  padding: 0;
}
.SitemapWrap_lowCategory {
  color: #33b5e5;
}
.SitemapWrap-show {
  transform: translateX(0);
}



