.Main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.Main_carusel-wrapper {
  overflow: hidden;
  height: 100vh;
  width: 100%;
  position: relative;
}
.Main_carousel {
  height: 100vh;
  width: 100%;
}
.Main_carousel_slide {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  background-image: url('../../static/images/bg/main.png');
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.Main_carousel_slide_img {
  height: 100%;
}
.Main_carousel_slide_content {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 70px;
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
}
.Main_carousel_slide_content h1 {
  color: #00C851;
  font-weight: bold;
  font-size: 4em;
  text-shadow: 0 2px 20px rgba(0, 0, 0, 0.3);
}
.Main_carousel_slide-2 {
  width: 100%;
  height: 100vh;
  background-image: url('../../static/images/bg/main2.jpg');
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
}
.Main_carousel_slide-3 {
  width: 100%;
  height: 100vh;
  background-image: url('../../static/images/bg/main3.jpg');
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
}
.Main_carousel_slide-4 {
  width: 100%;
  height: 100vh;
  background-image: url('../../static/images/bg/main4.jpg');
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: cover;
}
.Main_carousel .control-dots,
.Main_carousel .carousel .control-dots {
  position: absolute;
  bottom: 60px;
  right: 10vw;
  width: auto;
}
.Main_carousel .control-dots .dot,
.Main_carousel .carousel .control-dots .dot {
  background-color: #007bff;
  height: 15px;
  width: 15px;
}
.Main_title {
  padding-top: 20px;
  font-size: 5em;
  color: #00C851;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.2);
  font-family: 'Pangolin', cursive;
}



